.to-do {
	width: 200px;
	height: 300px;
}

.to-do h3 {
	margin: 0;
}

.to-do ul {
	padding-left: 20px;
}

.to-do li {
	list-style-type: "-";
	padding-left: 10px;
}

.new-task {
	margin-top: 8px;
}

.new-task input {
	height: 30px;
	padding-left: 5px;
	box-shadow: rgba(35, 35, 35, 0.84) 2px 2px 4px 0px inset,
		rgba(237, 237, 237, 0.217) -2px -2px 4px 1px inset;
	background-color: inherit;
	color: inherit;
	border: none;
	border-radius: 5px;
}

.add-btn {
	border: 0;
	background-color: inherit;
	color: inherit;
	padding-top: 1px;
	font-size: 25px;
}

.del-btn {
	border: none;
	background-color: inherit;
	color: inherit;
	cursor: pointer;
}

.fa-trash {
	font-size: 15px;
}
